import { Category } from './../../../models/category';
import { IFilter } from './../../../interfaces/ifilter';
import { ConfigService } from './../../../services/config.service';
import { CategoryService } from './../../../services/category.service';
import { Registry } from './../../../models/registry';
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ICategory } from 'src/app/interfaces/icategory';

import { filter, toNumber } from 'lodash-es';
import * as moment from 'moment'

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.css']
})
export class FilterComponent implements OnInit {

  @Input() showYears: boolean;
  @Input() showDateStartEnd: boolean;
  @Input() listOriginal: Registry[];

  @Output() filter: EventEmitter<Registry[]>

  public filterForm: IFilter;
  public locale: any;
  public listCategories: ICategory[];
  public listFiltered: Registry[];
  public years: number[];

  constructor(
    private cService: CategoryService,
    private config: ConfigService
  ) {
    this.listFiltered = [];
    this.filter = new EventEmitter<Registry[]>();
    // Inicializo con valores iniciales
    this.filterForm = {
      'idCategory': null,
      'dateStart': null,
      'dateEnd': null,
      'year': toNumber(moment().format("YYYY")), // Ao actual
      'quantityMin': null,
      'quantityMax': null
    }
    this.locale = this.config.locale;
    this.years = [];
  }

  ngOnInit() {

    // Obtengo las categorias
    this.cService.getCategories().subscribe(categories => {
      this.listCategories = categories;
    }, error => {
      console.error("Error al recuperar las categorias: " + error);
    })

    // Relleno los aos
    if(this.showYears){
      this.fillYears();
    }

  }

  /**
   * Relleno los aos, obtengo el ao inicial y el ao final de la configuracion
   */
  fillYears(){
    for (let year = this.config.yearStart; year <= this.config.yearEnd; year++) {
      this.years.push(year);
    }
  }

  /**
   * Filtra los datos
   */
  filterData(){

    // Reseteo la lista
    this.listFiltered = this.listOriginal;

    // Cantidad minima y cantidad maxima
    if(this.filterForm.quantityMin && this.filterForm.quantityMax){
      this.listFiltered = filter(this.listFiltered, r => {
        let q = toNumber(r.quantity);

        if(r.type === 'expense'){
          q = q * -1;
        }

        return q >= toNumber(this.filterForm.quantityMin) && q <= toNumber(this.filterForm.quantityMax); 
      })
      // Cantidad minima
    }else if(this.filterForm.quantityMin){
      this.listFiltered = filter(this.listFiltered, r => {
        let q = toNumber(r.quantity);

        if(r.type === 'expense'){
          q = q * -1;
        }

        return q >= toNumber(this.filterForm.quantityMin); 
      })
      // Cantidad maxima
    }else if(this.filterForm.quantityMax){
      this.listFiltered = filter(this.listFiltered, r => {
        let q = toNumber(r.quantity);

        if(r.type === 'expense'){
          q = q * -1;
        }

        return q <= toNumber(this.filterForm.quantityMax); 
      })
    }

    // Fecha inicio y fecha fin
    if(this.filterForm.dateStart && this.filterForm.dateEnd){
      this.listFiltered = filter(this.listFiltered, r => {
        return moment(r.date, 'YYYY-MM-DD').isSameOrAfter(moment(this.filterForm.dateStart, 'YYYY-MM-DD')) && 
                moment(r.date, 'YYYY-MM-DD').isSameOrBefore(moment(this.filterForm.dateEnd, 'YYYY-MM-DD'))
      })

    // Fecha inicio
    }else if(this.filterForm.dateStart){
      this.listFiltered = filter(this.listFiltered, r => {
        return moment(r.date, 'YYYY-MM-DD').isSameOrAfter(moment(this.filterForm.dateStart, 'YYYY-MM-DD'))
      })
      
    // Fecha fin
    }else if(this.filterForm.dateEnd){
      this.listFiltered = filter(this.listFiltered, r => {
        return moment(r.date, 'YYYY-MM-DD').isSameOrBefore(moment(this.filterForm.dateEnd, 'YYYY-MM-DD'))
      })
    }

    // Categoria
    if(this.filterForm.idCategory){
      this.listFiltered = filter(this.listFiltered, r => {
        return r.idCategory === this.filterForm.idCategory
      });
    }
    
    // Aos
    if(this.filterForm.year){
      this.listFiltered = filter(this.listFiltered, r => {
        return moment(r.date).year() === toNumber(this.filterForm.year);
      })
    }

    // Envio la lista filtrada
    this.filter.emit(this.listFiltered);

  }

}
