import { DetailComponent } from './../../../shared/components/detail/detail.component';
import { Category } from './../../../models/category';
import { CategoryService } from './../../../services/category.service';
import { Component, OnInit, Output, EventEmitter, ViewChild, TemplateRef, Input } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { categoryExists } from '../../../validators/validators';

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.css']
})
export class AddCategoryComponent implements OnInit {

  @ViewChild("modal_success", { static: false }) modal_success: TemplateRef<any>;
  @ViewChild("modal_error", { static: false }) modal_error: TemplateRef<any>;
  @ViewChild(DetailComponent, { static: false }) parentDetail: DetailComponent;

  @Input() categoryEdit: Category;
  @Input() listCategories: Category[];

  @Output() close: EventEmitter<boolean>;

  public formCategory: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private cService: CategoryService,
    private modalService: NgbModal
  ) {
    // Evento al cerrar el detalle
    this.close = new EventEmitter<boolean>();
  }

  ngOnInit() {

    if (this.categoryEdit) {
      // Valido si existe la categoria
      // Aado el id
      this.formCategory = this.formBuilder.group({
        name: new FormControl(this.categoryEdit.name, [Validators.required, categoryExists(this.listCategories)]),
        id: new FormControl(this.categoryEdit.id),
        user: new FormControl(this.categoryEdit.user)
      })
    } else {
      // Valido si existe la categoria
      this.formCategory = this.formBuilder.group({
        name: new FormControl('', [Validators.required, categoryExists(this.listCategories)]),
      })
    }


  }

  /**
   * Obtengo el formcontrol de name
   */
  get name() {
    return this.formCategory.get('name');
  }

  /**
   * Cierro el detalle
   * @param $event Estado de la ventana
   */
  closeDetail($event) {
    this.close.emit($event);
  }

  /**
   * Aade una categoria
   */
  addCategory() {

    // Obtengo la categoria del formgroup
    let category = new Category(this.formCategory.value);

    if (this.categoryEdit) {
      // Editar categoria
      this.cService.editCategory(category).then(() => {
        this.modalService.open(this.modal_success).result.then(() => {
          this.parentDetail.closeDetail();
        })
      }, error => {
        console.error(error);
        this.modalService.open(this.modal_error)
      })

    } else {
      // Aadir categoria
      this.cService.addCategory(category).then(() => {
        this.modalService.open(this.modal_success).result.then(() => {
          this.parentDetail.closeDetail();
        })

      }, error => {
        console.error(error);
        this.modalService.open(this.modal_error)
      })
    }



  }

}
