import { Category } from './category';
import { IRegistry } from './../interfaces/iregistry';
import { set, get } from 'lodash-es';

export class Registry implements IRegistry{

    constructor(data){
        set(this, 'data', data);
    }

    get id() {
        return get(this, 'data.id')
    }

    set id(value: string) {
        set(this, 'data.id', value)
    }

    get description() {
        return get(this, 'data.description')
    }

    set description(value: string) {
        set(this, 'data.description', value)
    }

    get idCategory() {
        return get(this, 'data.idCategory')
    }

    set idCategory(value: string) {
        set(this, 'data.idCategory', value)
    }

    get category() {
        return get(this, 'data.category')
    }

    set category(value: Category) {
        set(this, 'data.category', value)
    }

    get date() {
        return get(this, 'data.date')
    }

    set date(value: string) {
        set(this, 'data.date', value)
    }

    get type() {
        return get(this, 'data.type')
    }

    set type(value: string) {
        set(this, 'data.type', value)
    }

    get quantity() {
        return get(this, 'data.quantity')
    }

    set quantity(value: number) {
        set(this, 'data.quantity', value)
    }

    get user() {
        return get(this, 'data.user')
    }

    set user(value: string) {
        set(this, 'data.user', value)
    }

    getData(){
        return get(this, 'data');
    }

}
